import { HttpClient } from '@datavillage-me/http-util';
import { IDatasetServices } from '../../core/IDatasetServices';
import { Dataset, DatasetVersionUpdate, DatasetConfiguration, DatasetVersion } from '../../types';

import { HttpServiceClient } from '../http-client';

export class HttpDatasetServices extends HttpServiceClient implements IDatasetServices {
    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    getDatasets(): Promise<Dataset[]> {
        return this.httpClient.getJson(`/datasets`);
    }

    getDataset(id: string): Promise<Dataset> {
        return this.httpClient.getJson(`/datasets/${id}`);
    }

    createDataset(dataset: Dataset): Promise<Dataset> {
        return this.httpClient.postJson(`/datasets`, dataset);
    }

    addDatasetVersion(datasetId: string): Promise<DatasetVersion> {
        return this.httpClient.postJson(`/datasets/${datasetId}/version`);
    }

    getDatasetVersion(datasetId: string, version: number): Promise<DatasetVersion> {
        return this.httpClient.getJson(`/datasets/${datasetId}/versions/${version}`);
    }

    deprecateDatasetVersion(datasetId: string, version: number): Promise<Dataset> {
        return this.httpClient.postJson(`/datasets/${datasetId}/versions/${version}/deprecate`);
    }

    finalizeDatasetVersion(datasetId: string, version: number): Promise<Dataset> {
        return this.httpClient.postJson(`/datasets/${datasetId}/versions/${version}/finalize`);
    }

    updateDatasetVersion(datasetId: string, version: number, versionUpdate: DatasetVersionUpdate): Promise<Dataset> {
        return this.httpClient.postJson(`/datasets/${datasetId}/versions/${version}`, versionUpdate);
    }

    getDatasetConfiguration(datasetId: string, version: number): Promise<DatasetConfiguration> {
        return this.httpClient.getJson(`/datasets/${datasetId}/versions/${version}/configuration`);
    }
}
