// can't be too fancy with types (nested string literals) here to make mongoose accept everything.
export const allDataScopes = [
    'GenericPersonalData',
    'JudicicalPersonalData',
    'HealthData',
    'EthnicData',
    'GeneticData',
    'BiometricData',
    'SexualData',
    'OpinionData',
    'GenericNonPersonalData',
    'AnonymousData',
    'LegalPersonData'
] as const;
export type DataScope = (typeof allDataScopes)[number];

export const allStatuses = ['Final', 'Deprecated', 'Draft'] as const;
export type Status = (typeof allStatuses)[number];

export type DatasetConfiguration = {
    id: string;
    version: number;
    datasetKeys: { [key: string]: string };
};

export type GenericDatasetVersion = {
    version: number;
    status: Status;
    category?: string;
    description?: string;
    schema?: string;
};

export type DraftDatasetVersion = GenericDatasetVersion & {
    status: 'Draft';
    dataScope?: DataScope;
};

export type DatasetVersion =
    | (GenericDatasetVersion & {
          dataScope: DataScope;
      })
    | DraftDatasetVersion;

export type UpdateableDatasetVersion = Omit<DatasetVersion, 'status' | 'id' | 'version'>;
export type DatasetVersionUpdate = Partial<UpdateableDatasetVersion>;

export type Dataset = {
    id: string;
    name: string;
    owner: string;
    versions: DatasetVersion[];
};

export type DatasetVersionRef = {
    datasetId: string;
    version: number;
};

export type UpdateableDataset = Omit<Dataset, 'owner'>;

export type NewDataset = Omit<Dataset, 'id'>;
