// this module is used to produce the rollup lib
// it re-exposes the util and http-util modules as 1st class members of the api

export * from './index';

import * as util from '@datavillage-me/util';
import * as httputil from '@datavillage-me/http-util';

// this is required because otherwise the declaration bundling done by rollup-plugin-dts alters the signature of the assert function
// and causes this : https://github.com/microsoft/TypeScript/issues/41047
const fixed_util = {...util};

export { fixed_util as util, httputil };
