import { HttpClient } from '@datavillage-me/http-util';

import { HttpServiceClient } from '../http-client';
import {
    CageMetric,
    CollaboratorRole,
    DatasetVersionRef,
    ICollaborationSpace,
    ICollaborator,
    ICollaboratorCode,
    ICollaboratorData,
    INewCollaborationSpaceInvite,
    IUpdateInvite,
    NewCreateCollaborationSpace,
    NewDataDescriptor,
    NewIndividualDataDescriptor,
    PatchIndividuals,
    PatchIndividualsDataDescriptor,
    Resource,
    ResourceMetadata,
    UserScopeApplication,
    UserScopeApplicationWithUserData
} from '../../types';
import { ICollaborationSpacesServices, IndividualsConfig } from '../../core/ICollaborationSpacesServices';
import { ICageServices } from '../../core/ICageServices';
import { HttpCageServices } from '../cage-services';
import { HttpCageOperatorServices } from '../operator-services';
import { HTTP_FOLDER_CONTENTTYPE, ICageOperatorServices } from '../../core';
import { responseToResource, responseToResourceMetadata } from '../users-services/usersServices';

export class HttpCollaborationSpacesServices extends HttpServiceClient implements ICollaborationSpacesServices {
    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    getCollaborationSpaces(): Promise<ICollaborationSpace[]> {
        return this.httpClient.getJson(`/collaborationSpaces`);
    }

    createCollaborationSpace(collaborationSpace: NewCreateCollaborationSpace): Promise<ICollaborationSpace> {
        return this.httpClient.postJson(`/collaborationSpaces`, collaborationSpace);
    }

    updateCollaborationSpace(collaborationSpaceId: string, update: Partial<NewCreateCollaborationSpace>): Promise<ICollaborationSpace> {
        return this.httpClient.patchJson(`/collaborationSpaces/${collaborationSpaceId}`, update);
    }

    getCollaborationSpace(collaborationSpaceId: string): Promise<ICollaborationSpace> {
        return this.httpClient.getJson(`/collaborationSpaces/${collaborationSpaceId}`);
    }

    deleteCollaborationSpace(collaborationSpaceId: string): Promise<void> {
        return this.httpClient.delete(`/collaborationSpaces/${collaborationSpaceId}`);
    }

    addAllowedDatasetVersion(collaborationSpaceId: string, datasetVersionRef: DatasetVersionRef): Promise<ICollaborationSpace> {
        return this.httpClient.patchJson(`/collaborationSpaces/${collaborationSpaceId}/allowedData`, datasetVersionRef);
    }

    deleteAllowedDatasetVersion(collaborationSpaceId: string, datasetVersionRef: DatasetVersionRef): Promise<void> {
        return this.httpClient.delete(
            `/collaborationSpaces/${collaborationSpaceId}/allowedData?datasetId=${datasetVersionRef.datasetId}&version=${datasetVersionRef.version}`
        );
    }

    inviteCollaborator(collaborationSpaceId: string, invite: INewCollaborationSpaceInvite): Promise<ICollaborationSpace> {
        return this.httpClient.postJson(`/collaborationSpaces/${collaborationSpaceId}/collaborators`, invite);
    }

    getCollaborators(collaborationSpaceId: string): Promise<ICollaborator[]> {
        return this.httpClient.getJson(`/collaborationSpaces/${collaborationSpaceId}/collaborators`);
    }

    getCollaborator(collaborationSpaceId: string, clientId: string, role: CollaboratorRole): Promise<ICollaborator> {
        return this.httpClient.getJson(`/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}?role=${role}`);
    }

    updateCollaborator(
        collaborationSpaceId: string,
        clientId: string,
        role: CollaboratorRole.CodeProvider,
        update: Partial<ICollaboratorCode>
    ): Promise<ICollaboratorCode>;
    updateCollaborator(
        collaborationSpaceId: string,
        clientId: string,
        role: CollaboratorRole.DataConsumer | CollaboratorRole.DataProvider,
        update: Partial<ICollaboratorData>
    ): Promise<ICollaboratorData>;
    updateCollaborator(
        collaborationSpaceId: string,
        clientId: string,
        role: CollaboratorRole,
        update: Partial<ICollaborator>
    ): Promise<ICollaborator> {
        return this.httpClient.patchJson(`/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}?role=${role}`, update);
    }

    answerInvite(collaborationSpaceId: string, clientId: string, answer: IUpdateInvite): Promise<ICollaborator> {
        return this.httpClient.patchJson(`/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/invite`, answer);
    }

    createCollaboratorDataDescriptor(
        collaborationSpaceId: string,
        clientId: string,
        role: CollaboratorRole,
        dataDescriptor: NewDataDescriptor
    ): Promise<ICollaborationSpace> {
        return this.httpClient.postJson(
            `/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/dataDescriptors?role=${role}`,
            dataDescriptor
        );
    }

    deleteCollaboratorDataDescriptor(
        collaborationSpaceId: string,
        clientId: string,
        role: CollaboratorRole,
        dataDescriptorId: string
    ): Promise<void> {
        return this.httpClient.delete(
            `/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/dataDescriptors/${dataDescriptorId}?role=${role}`
        );
    }

    updateCollaboratorDataDescriptor(
        collaborationSpaceId: string,
        clientId: string,
        role: CollaboratorRole.DataProvider | CollaboratorRole.DataConsumer,
        dataDescriptorId: string,
        dataDescriptor: NewDataDescriptor
    ): Promise<ICollaborationSpace> {
        return this.httpClient.patchJson(
            `/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/dataDescriptors/${dataDescriptorId}?role=${role}`,
            dataDescriptor
        );
    }

    createIndividualDescriptor(
        collaborationSpaceId: string,
        clientId: string,
        individualDescriptor: NewIndividualDataDescriptor
    ): Promise<ICollaborationSpace> {
        return this.httpClient.postJson(
            `/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/individuals`,
            individualDescriptor
        );
    }

    deleteIndividualDescriptor(collaborationSpaceId: string, clientId: string, individualDescriptorId: string): Promise<void> {
        return this.httpClient.delete(
            `/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/individuals/${individualDescriptorId}`
        );
    }

    updateIndividualDescriptor(
        collaborationSpaceId: string,
        clientId: string,
        individualDescriptorId: string,
        update: PatchIndividualsDataDescriptor
    ): Promise<ICollaborationSpace> {
        return this.httpClient.patchJson(
            `/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/individuals/${individualDescriptorId}`,
            update
        );
    }

    updateIndividuals(collaborationSpaceId: string, clientId: string, update: PatchIndividuals): Promise<ICollaborationSpace> {
        return this.httpClient.patchJson(`/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/individuals`, update);
    }

    setupIndividuals(collaborationSpaceId: string, clientId: string, config: IndividualsConfig): Promise<ICollaborationSpace> {
        return this.httpClient.postJson(`/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/individuals/setup`, config);
    }

    getAll(): Promise<ICollaborationSpace[]> {
        return this.httpClient.getJson(`/collaborationSpaces`);
    }

    getAllApplications(): Promise<UserScopeApplication[]> {
        return this.httpClient.getJson(`/users/applications`);
    }

    getAllForUser(userId: string): Promise<UserScopeApplicationWithUserData[]> {
        return this.httpClient.getJson(`/users/${userId}/applications`);
    }

    getAllForDeveloper(developerId: string): Promise<ICollaborationSpace[]> {
        return this.httpClient.getJson(`/developers/${developerId}/spaces`);
    }

    getCageServices(applicationId: string): ICageServices {
        return new HttpCageServices(this.httpClient.getClientForSubpath(`/collaborationSpaces/${applicationId}/cage`));
    }

    getOperatorServices(applicationId: string): ICageOperatorServices {
        return new HttpCageOperatorServices(this.httpClient.getClientForSubpath(`/collaborationSpaces/${applicationId}/operator`));
    }

    addSpaceMetric(collaborationSpaceId: string, data: CageMetric['data']): Promise<void> {
        return this.httpClient.postJson(`/collaborationSpaces/${collaborationSpaceId}/metrics`, data);
    }

    getSpaceMetrics(collaborationSpaceId: string): Promise<CageMetric[]> {
        return this.httpClient.getJson(`/collaborationSpaces/${collaborationSpaceId}/metrics`);
    }

    deleteUserAppDataResource(collaborationSpaceId: string, userId: string, resourcePath: string): Promise<void> {
        return this.httpClient.delete(`/collaborationSpaces/${collaborationSpaceId}/users/${userId}/appdata/${resourcePath}`);
    }

    async getUserAppDataMetadata(collaborationSpaceId: string, userId: string, resourcePath: string): Promise<ResourceMetadata> {
        const response = await this.httpClient.head(`/collaborationSpaces/${collaborationSpaceId}/users/${userId}/appdata/${resourcePath}`);
        return responseToResourceMetadata(response);
    }

    async getUserAppDataResource(collaborationSpaceId: string, userId: string, resourcePath: string): Promise<Resource> {
        const response = await this.httpClient.authorizedFetch(
            `/collaborationSpaces/${collaborationSpaceId}/users/${userId}/appdata/${resourcePath}`
        );
        return responseToResource(response);
    }

    saveUserAppDataResource(
        collaborationSpaceId: string,
        userId: string,
        resourcePath: string,
        content: string | undefined,
        contentType: string | undefined
    ): Promise<Resource> {
        // pro-actively add the folder content-type if we have a trailing slash
        if (!contentType && resourcePath.endsWith('/')) contentType = HTTP_FOLDER_CONTENTTYPE;

        const headers = contentType ? { 'Content-Type': contentType } : undefined;

        return this.httpClient
            .authorizedFetch(`/collaborationSpaces/${collaborationSpaceId}/users/${userId}/appdata/${resourcePath}`, {
                headers,
                method: 'PUT',
                body: content
            })
            .then(responseToResource);
    }

    pushConfiguration(collaborationSpaceId: string, clientId: string, dataDescriptorId: string, content: Blob): Promise<Response> {
        return this.httpClient.put(
            `/collaborationSpaces/${collaborationSpaceId}/collaborators/${clientId}/dataDescriptors/${dataDescriptorId}/configuration`,
            content
        );
    }
}
