import { HttpClient } from '@datavillage-me/http-util';
import { IConnectorServices } from '../../core/IConnectorServices';
import { Connector } from '../../types';

import { HttpServiceClient } from '../http-client';

export class HttpConnectorServices extends HttpServiceClient implements IConnectorServices {

    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    getConnectors(): Promise<Connector[]> {
        return this.httpClient.getJson(`/connectors`);
    }

}
