import { HttpClient } from '@datavillage-me/http-util';

import { HttpServiceClient } from '../http-client';
import { AuthenticationInfo, Developer, PostDevelopersRoles } from '../../types';
import { DEV_CONNECT_ACTION, IDevelopersServices } from '../../core';
import { DEVELOPER_IDENTITY_PROVIDER } from '../../types/authentication';

export class HttpDevelopersServices extends HttpServiceClient implements IDevelopersServices {
    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    create(auth: AuthenticationInfo<DEVELOPER_IDENTITY_PROVIDER>): Promise<Developer> {
        return this.httpClient.postJson<Developer>(`/developers/`, auth);
    }

    delete(id: string): Promise<void> {
        return this.httpClient.delete(`/developers/${id}`);
    }

    getAll(): Promise<Developer[]> {
        return this.httpClient.getJson(`/developers`);
    }

    getById(id: string): Promise<Developer> {
        return this.httpClient.getJson(`/developers/${id}`);
    }

    getAuthenticationUrl(identityProvider: DEVELOPER_IDENTITY_PROVIDER, activationCallbackUrl: string): string {
        const url = new URL(this.httpClient.getUrl('/auth/dev/connect/' + identityProvider));
        url.search = new URLSearchParams({
            activationCallbackUrl,
            action: DEV_CONNECT_ACTION.DEVELOPER_AUTHENTICATION
        }).toString();
        return url.toString();
    }

    updateRoles(update: PostDevelopersRoles): Promise<Developer[]> {
        return this.httpClient.postJson<Developer[]>(`/developers/roles`, update);
    }

    getIssuer(clientLabel: string): Promise<string> {
        return this.httpClient.get(`/auth/dev/issuer/${clientLabel}`).then(resp => resp.text());
    }
}
