import { HttpClient, initSearchParams } from '@datavillage-me/http-util';

import { HttpServiceClient } from '../http-client';
import { Activity } from '../../types';
import { ActivityLogService, ActivitySearchQuery } from '../../core/ActivityLogService';

/**
 * Remote HTTP implementation of ActivityLogService
 */
export class HttpActivityLogServices extends HttpServiceClient implements ActivityLogService {
    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    getActivityLog(query: ActivitySearchQuery): Promise<Activity[]> {
        const urlQuery = initSearchParams(query);

        return this.httpClient.getJson(`/activities?` + urlQuery.toString());
    }
}
