import { HttpClient, initSearchParams } from '@datavillage-me/http-util';

import { HttpServiceClient } from '../http-client';
import { JobSearchQuery, JobsService } from '../../core/JobService';
import { Job, JobRequest } from '../../types';

/**
 * Remote HTTP implementation of ActivityLogService
 */
export class HttpJobsServices extends HttpServiceClient implements JobsService {
    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    getJobs(query: JobSearchQuery): Promise<Job[]> {
        const urlQuery = initSearchParams(query);

        return this.httpClient.getJson(`/jobs?` + urlQuery.toString());
    }

    getJob(id: string): Promise<Job> {
        return this.httpClient.getJson(`/jobs/` + id);
    }

    startJob(request: JobRequest): Promise<Job> {
        return this.httpClient.postJson(`/jobs`, request);
    }

    createJob(jobContent: Pick<Job, 'userId' | 'consentId' | 'request'>): Promise<Job> {
        // this uses the same HTTP endpoint as startJob
        return this.httpClient.postJson(`/jobs`, jobContent);
    }

    logJobActivity(jobId: string, update: Partial<Pick<Job, 'status' | 'message' | 'progress' | 'result'>>): Promise<Job> {
        return this.httpClient.postJson(`/jobs/` + jobId, update);
    }
}
