/**
 * A list of well-known prefixes and their corresponding namespace URIs, as used in DV object models
 */
import { assert } from '@datavillage-me/util';

export enum WK_NAMESPACE_URIS {
    dv = 'https://api.datavillage.me/',
    schema = 'https://schema.org/',
    gConsent = 'https://w3id.org/GConsent#',
    dvConsent = 'https://datavillage.me/consent#',
    consents = 'https://api.datavillage.me/consents/'
}

export enum CONTENT_TYPES {
    text = 'text/plain',
    turtle = 'text/turtle',
    rdf_xml = 'application/rdf+xml',
    json = 'application/json',
    nq = 'application/n-quads',
    n3 = 'text/n3',
    nt = 'application/n-triples'
}

export function reduceWithWellKnownPrefix(uri: string) {
    const matchingEntry = Object.entries(WK_NAMESPACE_URIS).find(([prefix, value]) => uri.startsWith(value));

    if (!matchingEntry) throw new Error('Cannot reduce uri with a well know prefix: ' + uri);

    return matchingEntry[0] + ':' + uri.substring(matchingEntry[1].length);
}

export function expandWellKnownPrefix(uri: string) {
    const matchingEntry = Object.entries(WK_NAMESPACE_URIS).find(([prefix, value]) => uri.startsWith(prefix + ':'));

    if (!matchingEntry) throw new Error('Cannot expand uri from a well know prefix: ' + uri);

    return matchingEntry[1] + uri.substring(matchingEntry[0].length + 1);
}

export function prependNamespacePrefix(prefix: keyof typeof WK_NAMESPACE_URIS, path: string) {
    // that's a very aggressive check, but this should be applied to internal controlled values that do not contain colons
    if (path.includes(':')) throw new Error('Path already appears to be a URI: ' + path);

    return prefix + ':' + path;
}

export function getValueInNamespace(prefix: keyof typeof WK_NAMESPACE_URIS, uri: string) {
    assert(prefix in WK_NAMESPACE_URIS, 'Unknown prefix : ' + prefix);

    if (uri.startsWith(prefix + ':')) {
        return uri.substring(prefix.length + 1);
    } else if (uri.startsWith(WK_NAMESPACE_URIS[prefix])) {
        // check for fully qualified namespace
        return uri.substring(WK_NAMESPACE_URIS[prefix].length);
    } else {
        throw new Error(`Value is not in namespace ${prefix} : ${uri}`);
    }
}
