import { HttpServiceClient } from '../http-client';
import { DataBridge, ImportDataParams, ImportDataResult } from '../../core/DataBridge';
import { DataSourceMetadata, Job } from '../../types';

export class HttpDataBridge extends HttpServiceClient implements DataBridge {
    getDataSources(): Promise<DataSourceMetadata[]> {
        return this.httpClient.getJson('/sources/providers');
    }

    importData(
        userIds: string[] | undefined,
        dataSourceId: string | undefined,
        clientId?: string,
        importParams?: ImportDataParams
    ): Promise<ImportDataResult> {
        return this.httpClient.postJson('/sources/importData', {
            userIds,
            dataSourceId,
            importParams,
            clientId
        });
    }

    async startImportJob(
        userIds: string[] | undefined,
        dataSourceId: string | undefined,
        clientId?: string,
        importParams?: ImportDataParams
    ): Promise<Job[]> {
        return this.httpClient.postJson('/sources/startImportJob', {
            userIds,
            dataSourceId,
            clientId,
            importParams
        });
    }
}
