import { HttpClient } from '@datavillage-me/http-util';
import { AlgoLog, CageResponse, CageStatusLog, DeployLog, ICageOperatorServices, IOperatorServices, LokiResponse, OperatorResponse } from '../../core/IOperatorServices';
import { FullAlgoSettings } from '../../types/operator';

import { HttpServiceClient } from '../http-client';

export class HttpOperatorServices extends HttpServiceClient implements IOperatorServices {
    static forApp(operatorUrl: string): HttpOperatorServices {
            return new HttpOperatorServices(new HttpClient(new URL(`${operatorUrl}`).toString()));
    }

    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    getCages(): Promise<CageResponse[]> {
        return this.httpClient.getJson(`/operator/cages`);
    }

}

export class HttpCageOperatorServices extends HttpServiceClient implements ICageOperatorServices {

    static forApp(operatorUrl: string, appId: string): HttpCageOperatorServices {
             return new HttpCageOperatorServices(new HttpClient(new URL(`${operatorUrl}/${appId}`).toString()))
    }

    deploy(body?: FullAlgoSettings): Promise<OperatorResponse> {
        return this.httpClient.postJson(`/deploy`, body);
    }
    rollout(): Promise<{ message: 'string' }> {
        return this.httpClient.postJson(`/rollout`);
    }
    delete(keep_vm?: boolean, keep_data?: boolean): Promise<{ message: 'string' }> {
        return this.httpClient.deleteJson(`/delete`, { query: { keep_vm, keep_data } });
    }
    getPodStatus(): Promise<any> {
        return this.httpClient.getJson(`/logs/pod_status`);
    }
    getAlgoLogs(tailLines?: number): Promise<AlgoLog[]> {
        return this.httpClient.getJson(`/logs/algo?tail_lines=${tailLines || 100}`);
    }
    getDeploymentLogs(tailLines?: number): Promise<DeployLog[]> {
        return this.httpClient.getJson(`/logs/deployment?tail_lines=${tailLines || 100}`);
    }
    getCageStatus(tailLines?: number): Promise<CageStatusLog[]> {
        return this.httpClient.getJson(`/logs/states?tail_lines=${tailLines || 100}`);
    }
    getLoki(query: any, start?: Date): Promise<LokiResponse> {
        return this.httpClient.getJson(`/loki/api/v1/query_range`, { query: { query, start: start?.toISOString() } });
    }
    sendLokiEvents(streams: { stream: Record<string, string>; values: [number, string][] }[]): Promise<void> {
        return this.httpClient.postJson(`/loki`, streams);
    }
}


