import { MappingExtractionResult } from '../core';

export enum DATA_SOURCE_TYPE {
    PULL = 'PULL',
    PUSH = 'PUSH'
}

/**
 * Entry in a user's datasources.json file, describing an activated data provider
 */
export type DatasourceRecord = {
    activationDate: string;
    // TODO harmonize this with the consent model, iu.e. either URIs or not
    /**
     * The id of the activated data provider. Not a URI, but just a plain text id, e.g. 'spotify'
     */
    resourceUri: string;
    userInfo: ProviderUserInfo;
    grantedScopes: string[];

    /**
     * The time-ordered statistics of previous harvest jobs.
     * Most recent first.
     */
    statistics?: HarvestStatistics[];
};

export type HarvestStatistics = {
    date: string;
    totalItems: number;
    mappings: MappingExtractionResult[];
};

// TODO this should be part of Identity in ../users.ts
export type ProviderUserInfo = {
    id: string;
    name?: string;
    email?: string;
    displayName?: string;
    profileUrl?: string;

    // required for Pod Providers
    issuer?: string;
};

export type PodProviderUserInfo = ProviderUserInfo & {
    // required for Pod Providers
    issuer: string;
};
export type FetcherParams = {
    accessToken: string;
};

/**
 * Metadata for a single mapping within a data provider
 */
export type DataSourceMappingMetadata = {
    // id will be used to generate file name
    id: string;
    podFile: string;
    label: string;
    /**
     * Fully qualified URIs describing the kind of data available through this mapping, e.g. 'https://schema.org/LikeAction'
     */
    categories: string[];
};

/**
 * Metadata about a data source declared in the system.
 * Subset of DataSourceMappingDescriptor, suitable for exchange outside the backend
 * DataSourceMetadata are stored on dynamo
 */
export type DataSourceMetadata = {
    /**
     * The id of a data source is currently not a URI, but just a plain text id, e.g. 'spotify'
     */
    id: string;
    applicationId?: string;
    type: DATA_SOURCE_TYPE;
    enabled?: boolean;
    label: string;
    url: string;
    podFolder: string;
    scope?: string[];
    mappings: DataSourceMappingMetadata[];
};

/**
 * Extension of DataSourceMetadata that contains non-serializable, backend-only information
 * such as token refresher functions and mapping functions
 * PullDataSourceDescriptor are stored in data-providers package
 */
export type PullDataSourceDescriptor = {
    id: string;
    fetchUserInfo: (refresh_token: string) => Promise<ProviderUserInfo>;

    /**
     * Fetches a new accessToken, and possibly a refreshToken, from the remote datasource auth mechanism
     * The tokenRefresher is optional ; some datasources do not have a mechanism to produce access tokens, and need
     * the access token to be passed along with each importData request
     * @param refresh_token
     */
    tokenRefresher?: (refresh_token: string) => Promise<{ access_token: string; refresh_token?: string }>;
    mappings: PullDataSourceMappingDescriptor[];
};

export type PullDataSourceMappingDescriptor = {
    // TODO should support plain URL fetch, with mere token replacement ?
    // fetchUrl?: string;
    id: string;
    fetch: (params: FetcherParams) => Promise<Iterable<any>>;
    mappingPaginationSize?: number;
} & (
    | // the mapping mechanism can either be done with ttl or jsonld framing
    { ttl: string | (() => Promise<string>) }
    | { frame: any }
);

export type DataSource = DataSourceMetadata &
    Partial<PullDataSourceDescriptor> & {
        mappings: DataSourceMapping;
    };

/**
 * Extension of DataSourceMappingMetadata that contains non-serializable, backend-only information.
 * Partial<PullDataSourceMappingDescriptor> as DataSource of type PUSH only need DataSourceMappingMetadata
 */
export type DataSourceMapping = DataSourceMappingMetadata & Partial<PullDataSourceMappingDescriptor>;

export type ApplicationDescriptor = {
    id: string;
    recentActivity?: ApplicationProcessSummary[];
};

export type ApplicationProcessSummary = {
    timestamp: string;
    total: number;
    newItems: string[];
};
