
import { Identity, PodDescriptor, USER_IDENTITY_PROVIDER } from "./authentication";

export enum UserRole {
    ADMIN = 'ADMIN',
    INFRA = 'INFRA',
    USER = 'USER'
}

export type UserDetails = {
    id: string;
    identities: Identity<USER_IDENTITY_PROVIDER>[];
    clientId?: string; // TODO this is optional unless we force the clientId as arguments of createUser
    creationDate: string;
    updateDate: string;
    masterPodUri: string;
    pods: PodDescriptor[];
    role?: UserRole;
};
