export const JupyterTools = {
    authenticate: async (apiUrl: string, displayHtmlFn: (html: string) => void, type?: POD_IDENTITY_PROVIDER, issuer?: string) => {
        const authUrl = await getRemoteClient(apiUrl).getPassport().getAuthenticationUrl('POSTMESSAGE', type, issuer);

        const html = `
            <div id='dv_auth'>
              <!-- <span id='dv_prov_choice'><button onclick="">Google</button> <button>Inrupt</button> <button>Solid Community</button></span> -->
              <span id='dv_auth_msg'>Authenticating...</span>
              <span id='dv_auth_result' style="display:none">
                <input type="text" id="dv_jwt">
                <span id="dv_jwt_copy_btn">Copy</span>
              </span>
            </div>
            <script>
            {
              function authWithProvider() {
                  document.getElementById('dv_jwt').select();
                  document.execCommand("copy");
              }
                
              function copyToClipboard() {
                  document.getElementById('dv_jwt').select();
                  document.execCommand("copy");
              }
            
              function receiveAuthMessage(event) {
                document.getElementById('dv_auth_msg').innerText = '';
                document.getElementById('dv_jwt').value = event.data;
                document.getElementById('dv_auth_result').style.display='';
                document.getElementById('dv_jwt_copy_btn').onclick = copyToClipboard;
                authWdw && authWdw.close();
                window.removeEventListener("message", receiveAuthMessage);
              }
              
              window.addEventListener("message", receiveAuthMessage);
              let authWdw = window.open('${authUrl}', "dvauth");
            }
            </script>
        `;

        displayHtmlFn(html);
    },

    activateSource: async (apiUrl: string, displayHtmlFn: (html: string) => void, type: DATASOURCE_IDENTITY_PROVIDER) => {
        const authUrl = await getRemoteClient(apiUrl).getPassport().getDatasourceActivationUrl('POSTMESSAGE', type);

        const html = `
            <div id='dv_auth'>
              <span id='dv_auth_msg'>Authenticating...</span>
              <span id='dv_auth_result' style="display:none">
              </span>
            </div>
            <script>
            {
              function receiveAuthMessage(event) {
                document.getElementById('dv_auth_result').innerText = event.data;
                document.getElementById('dv_auth_result').style.display='';
                authWdw && authWdw.close();
                window.removeEventListener("message", receiveAuthMessage);
              }
              
              window.addEventListener("message", receiveAuthMessage);
              let authWdw = window.open('${authUrl}', "dvauth");
            }
            </script>
        `;

        displayHtmlFn(html);
    }
};

// these are needed in the lambda implementations
export { responseToResourceMetadata, responseToResource } from './http/users-services/usersServices';

export type RemoteClient = Client & {
    httpClient: HttpClient;
};

/**
 * Get an instance of a client for the given URL.
 * @param apiUrlStr the URL of the remote API
 * @param idToken (optional) a token to be used to authenticate with the API
 */
export function getRemoteClient(apiUrlStr: string, idToken?: string, authMode?: AUTH_MODE): RemoteClient {
    const apiURL = new URL(apiUrlStr);
    if (apiURL.protocol == 'mock:') {
        // if using the mock API, the URL should be of the form "mock://[user]"

        const userId = apiURL.pathname;
        //Mock-up client for dev purposes
        return getMockClient(userId);
    } else {
        const httpClient = new HttpClient(apiUrlStr, idToken, authMode);
        return {
            httpClient,
            getConsentsServices: () => new HttpConsentsServices(httpClient),
            getConsentReceiptsServices: () => new HttpConsentReceiptsServices(httpClient),
            getCredentialManager: () => new HttpCredentialManager(httpClient),
            getDataBridge: () => new HttpDataBridge(httpClient),
            getPassport: () => new HttpPassport(httpClient),
            getUsersServices: () => new HttpUsersServices(httpClient),
            getUsersMgmtService: () => new HttpUsersMgmtServices(httpClient),
            getClientsServices: () => new HttpClientsServices(httpClient),
            getActivityLog: () => new HttpActivityLogServices(httpClient),
            getJobsService: () => new HttpJobsServices(httpClient),
            getDevelopersServices: () => new HttpDevelopersServices(httpClient),
            getCollaborationSpacesServices: () => new HttpCollaborationSpacesServices(httpClient),
            getOperatorServices: () => new HttpOperatorServices(httpClient),
            getDatasetServices: () => new HttpDatasetServices(httpClient),
            getConnectorServices: () => new HttpConnectorServices(httpClient)
        };
    }
}
