import { DatasetVersionRef } from './dataset';
import { EnvironmentSettings, AlgoSettings, GeneralSettings } from './operator';
import { assert, DistributiveOmit } from '@datavillage-me/util';

/**
 *
 */
export type IAlgoSettings = AlgoSettings & {
    updateDate: Date;
    createDate: Date;
};

export enum InviteStatus {
    'Created' = 'Created',
    'Accepted' = 'Accepted',
    'Declined' = 'Declined'
}

export enum CollaboratorRole {
    'CodeProvider' = 'CodeProvider',
    'DataProvider' = 'DataProvider',
    'DataConsumer' = 'DataConsumer'
}

export enum DataDescriptorKind {
    'Dataset' = 'Dataset',
    'Custom' = 'Custom'
}

export interface IInvite {
    invitedBy: string; // developerId
    invitedAt?: Date;
    answeredBy?: string; // developerId
    answeredAt?: Date;
    status: InviteStatus;
}

export enum DataType {
    'Pull' = 'Pull',
    'Push' = 'Push'
}

export interface IDataDescriptorCommon {
    id: string;
    settings: { connector?: string } & EnvironmentSettings;
    kind: DataDescriptorKind;
    label?: string;
}

export type IDatasetDataDescriptor = IDataDescriptorCommon & {
    dataset: string;
    version: number;
    kind: DataDescriptorKind.Dataset;
};

export type ICustomDataDescriptor = IDataDescriptorCommon & {
    type: DataType;
    name?: string;
    description?: string;
    schema?: string;
    syntheticData: string;
    apiUrl?: string;
    kind: DataDescriptorKind.Custom;
};

export type IDataDescriptor = ICustomDataDescriptor | IDatasetDataDescriptor;

export const isCustomDataDescriptor = (descriptor: IDataDescriptorCommon): descriptor is ICustomDataDescriptor => {
    return descriptor.kind === DataDescriptorKind.Custom;
};

export type NewDataDescriptor = DistributiveOmit<IDataDescriptor, 'id'>;

export type ICollaboratorCommon = {
    clientId: string;
    invite: IInvite;
};

export type ICollaboratorData = ICollaboratorCommon & {
    role: CollaboratorRole.DataConsumer | CollaboratorRole.DataProvider;
    dataDescriptors: IDataDescriptor[];
};

export type ICollaboratorCode = ICollaboratorCommon & {
    role: CollaboratorRole.CodeProvider;
    algoSettings: IAlgoSettings;
    settings: EnvironmentSettings;
    generalSettings: GeneralSettings;
    schema?: string;
};

export type ICollaborator = ICollaboratorData | ICollaboratorCode;

export enum IndividualDataDescriptorType {
    'In' = 'In',
    'Out' = 'Out'
}

export interface IIndividualDataDescriptor {
    id: string;
    clientId: string;
    type: IndividualDataDescriptorType;
    settings: EnvironmentSettings;
    schema?: string;
}

export type NewIndividualDataDescriptor = Pick<IIndividualDataDescriptor, 'type' | 'settings' | 'schema'>;
export type PatchIndividualsDataDescriptor = Pick<IIndividualDataDescriptor, 'settings' | 'schema'>;

export interface IIndividuals {
    enabled: boolean;
    dataDescriptors: IIndividualDataDescriptor[];
}

export type PatchIndividuals = Pick<IIndividuals, 'enabled'>;

export interface ICollaborationSpace {
    id: string;
    owner: string; // clientId
    createdBy: string; // developerId
    name: string;
    category: string;
    description: string;

    appSettings?: {
        logoUrl?: string;
        appUrl: string;
        podFolder: string;
        allowedCallbacks: string[];
    };
    extensionSettings?: {
        urlRegex: string;
        componentIdentifier: string;
        classNameToPrepend: string;
    };
    enclaving: {
        enabled: boolean;
    };
    sandboxing: {
        enabled: boolean;
        allowed: string[];
    };
    collaborators: ICollaborator[];
    individuals: IIndividuals;
    creationDate: string;
    allowedDatasetVersions?: DatasetVersionRef[];
}

export interface INewCollaborationSpaceInvite {
    clientId: string;
    role: CollaboratorRole;
}

export interface IUpdateInvite {
    role: CollaboratorRole;
    status: InviteStatus.Accepted | InviteStatus.Declined;
}

export type NewCreateCollaborationSpace = Pick<
    ICollaborationSpace,
    | 'owner'
    | 'name'
    | 'category'
    | 'description'
    | 'appSettings'
    | 'extensionSettings'
    | 'enclaving'
    | 'sandboxing'
    | 'allowedDatasetVersions'
>;

export function isCodeCollaborator(c: ICollaborator): c is ICollaboratorCode {
    return c.role == CollaboratorRole.CodeProvider;
}

export function isDataCollaborator(c: ICollaborator): c is ICollaboratorData {
    return c.role == CollaboratorRole.DataProvider || c.role == CollaboratorRole.DataConsumer;
}

export function getSpaceAlgo(space: ICollaborationSpace) {
    const codeCollaborators = space.collaborators.filter(isCodeCollaborator);

    assert(codeCollaborators.length <= 1, 'Space cannot have multiple code collaborators');

    return codeCollaborators.length ? codeCollaborators[0] : undefined;
}

export type CageMetric = {
    timestamp: string;
    spaceId: string;
    data: any;
};
