// CLIENT-[ID]-MGR
// CLIENT-[ID]-MMB
// CLIENT-[ID]-CS-[ID]-FUNC
// CLIENT-[ID]-CS-[ID]-TECH

const roleRegEx = 'ADMIN|CLIENT-([a-z0-9]+)-(MMB|MGR|((CS)-([a-z0-9]+)-(FUNC|TECH)))';

export type DevRoleClient = {
    clientId: string;
    role: 'MGR' | 'MMB';
};

export type DevRoleSpace = {
    clientId: string;
    role: 'FUNC' | 'TECH';
    spaceId: string;
};

export type DevRole = DevRoleClient | DevRoleSpace;

export const isRoleValid = (role: string) => {
    return !!new RegExp(roleRegEx).test(role);
};

export const parseRole = (role: string): DevRole => {
    const execResult = new RegExp(roleRegEx).exec(role);

    if (execResult) {
        const [, clientId, clientRole, , CS, spaceId, spaceRole] = execResult;

        if (!CS && (clientRole == 'MGR' || clientRole == 'MMB')) {
            return {
                clientId,
                role: clientRole
            };
        } else if (CS && (spaceRole == 'FUNC' || spaceRole == 'TECH')) {
            return {
                clientId,
                spaceId,
                role: spaceRole
            };
        } else {
            throw new Error('Invalid role string');
        }
    } else {
        // TODO fix this: triggered when role ADMIN
        throw new Error('Invalid role string - syntax error');
    }
};

export function role2string(role: DevRole) {
    return role.role == 'FUNC' || role.role == 'TECH' ?
      `CLIENT-${role.clientId}-CS-${role.spaceId}-${role.role}` :
      `CLIENT-${role.clientId}-${role.role}`;
}

/**
 * Find a role that matches one of the provided role patterns.
 * @param devRoles
 * @param requiredRoles
 */
export const findMatch = (devRoles: DevRole[], requiredRoles: Partial<DevRole>[]): DevRole | undefined => {
    const match = devRoles.find(role =>
        requiredRoles.find(
            requiredRole =>
                (requiredRole.clientId == undefined || requiredRole.clientId == role.clientId) &&
                (requiredRole.role == undefined || requiredRole.role == role.role) &&
                ( !('spaceId' in requiredRole) || (('spaceId' in role) && requiredRole.spaceId == role.spaceId))
        )
    );

    return match;
};

export const getClientId = (role: string) => {
    const execResult = new RegExp(roleRegEx).exec(role);
    return execResult ? execResult[1] : undefined;
};

export const getCollaborationSpaceId = (role: string) => {
    const execResult = new RegExp(roleRegEx).exec(role);
    return execResult ? execResult[4] : undefined;
};
