import { HttpClient } from '@datavillage-me/http-util';

import { HttpServiceClient } from '../http-client';
import { ICageServices, STREAMS } from '../../core/ICageServices';
import { sanitizePath } from '@datavillage-me/util';

export class HttpCageServices extends HttpServiceClient implements ICageServices {
    static forApp(operatorUrl: string, appId: string): HttpCageServices {
        return new HttpCageServices(new HttpClient(new URL(sanitizePath(appId, 'NOSLASH', 'ABSOLUTE'), operatorUrl).toString()));
    }

    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    getEvents(stream_name: STREAMS = STREAMS.events): Promise<any[]> {
        return this.httpClient.getJson(`/event`, { query: { stream_name } });
    }

    postEvent(event: any, stream_name: STREAMS = STREAMS.events): Promise<any> {
        return this.httpClient.postJson(`/event`, event, { query: { stream_name } });
    }

    getAttestation(): Promise<any> {
        return this.httpClient.getJson(`/attestation_SEV`);
    }

    uploadData(filename: string, data: Blob): Promise<string> {
        return this.httpClient.put(`/resources/data/`+filename, data).then(resp => resp.text());
    }

    getOutput(filename: string): Promise<Blob> {
        return this.httpClient.get(`/resources/output/`+filename).then(resp => resp.blob());
    }

    listData(): Promise<{ filenames: string[] }> {
        return this.httpClient.getJson(`/resources/data/`);
    }

    listOutputs(): Promise<{
        files: [{file: string, size: number, atime: number, mtime: number, ctime: number}];
    }> {
        return this.httpClient.getJson(`/resources/outputs/`);
    }



}
