export type MediaObject = {
    id: string;
    name?: string;
    keywords?: string;
    description?: string;
    about?: string;
    provider?: string;
    url?: string;
};

export type MusicAlbum = MediaObject & {
    //type: "https://schema.org/MusicAlbum",
    byArtist?: MusicGroup;
};

export type MusicGroup = MediaObject & {
    //type: "https://schema.org/MusicGroup",
};

export type MusicRecording = MediaObject & {
    //type: "https://schema.org/MusicRecording",
    inAlbum?: MusicAlbum;
    byArtist?: MusicGroup;
};

export type MediaAction<T extends MediaObject = MediaObject> = {
    id: string;
    type: string;
    additionalType: string[];
    startTime: string;
    object: T;
};

export async function parseRecommandationsRdf(rdfStrings: (string | undefined)[]): Promise<Recommendation[]> {
    return frameRdfStrings(rdfStrings, recoFrame);
}

export async function frameRdfStrings<T extends NodeObject = any>(rdfStrings: (string | undefined)[], frame: Frame): Promise<T[]> {
    const fullString = rdfStrings.filter(str => !!str).join('\n');

    if (fullString) {
        // jsonld only accepts nquads/ntriples - first parse with rdflib to ensure ntriples format
        const store = graph();
        parse(fullString, store, WK_NAMESPACE_URIS.dv, CONTENT_TYPES.turtle);

        return frameRdfGraph(store, frame);
    } else {
        return [];
    }
}

export async function frameRdfGraph<T extends NodeObject = any>(graph: IndexedFormula, frame: Frame): Promise<T[]> {
    const nquads = await serialize(null as any, graph, WK_NAMESPACE_URIS.dv, CONTENT_TYPES.nt, undefined, { flags: 'n' });

    const jsonldRecos = await jsonld.fromRDF(nquads as any, {
        format: CONTENT_TYPES.nq,
        useNativeTypes: true
    });
    const framedJson = await jsonld.frame(
        jsonldRecos,
        frame,

        // @ts-ignore : omitGraph is not yet in @types/jsonld
        { omitGraph: false }
    );

    return (framedJson['@graph'] || []) as T[];
}

export async function reverseFrame(object: any, frame: Frame): Promise<string> {
    if (typeof frame == 'string') throw new Error('Frame by URL not supported');

    const ctx = Array.isArray(frame['@context']) ? frame['@context'][0] : frame['@context'];
    if (typeof ctx == 'string') throw new Error('Context by URL not supported');

    //const rdf = await jsonld.flatten({"@graph": object
    //}, ctx || undefined)

    //@ts-ignore
    return jsonld.normalize({ '@context': frame['@context'], '@graph': object }, {safe: false});
}
