import {
    ApplicationDescriptor,
    ApplicationProcessSummary,
    DatasourceRecord,
    HarvestStatistics,
    ProviderUserInfo,
    UserDetails
} from '../types';
import { _encryptStringWithRsaPublicKey, assert, Decorator, sanitizePath } from '@datavillage-me/util';
import { _404_undefined, HttpError } from '@datavillage-me/http-util';
import crypto from 'crypto';
import {
    AuthenticationInfo,
    Identity,
    POD_IDENTITY_PROVIDER,
    PodDescriptor,
    USER_IDENTITY_PROVIDER
} from "../types/authentication";
import { assertResourceType, FileResource, FolderResource, Resource, RESOURCE_TYPE, ResourceMetadata, MasterPod, Pod } from '../types/pod';

export const MASTER_POD_ALIAS: '$MASTER$' = '$MASTER$';

// TODO for the time being, we use a custom content-type to advertise folders over HTTP
//      this is necessary because of this AWS bug : https://forums.aws.amazon.com/message.jspa?messageID=907910
export const HTTP_FOLDER_CONTENTTYPE = 'application/vnd.datavillage.folder+json';

/**
 * In any HTTP operation, the path of a resource defines whether it's a folder, based on the presence of a trailing slash
 */
export interface IBasicUsersServices {
    getUser: (userId: string) => Promise<UserDetails>;
    getPods: (userId: string) => Promise<PodDescriptor[]>;
    getPod: (userId: string, podId: string) => Promise<PodDescriptor>;
    getResourceMetadata: (userId: string, podId: string, resourcePath: string) => Promise<ResourceMetadata>;
    getResource: (userId: string, podId: string, resourcePath: string) => Promise<Resource>;
    saveResource: (
        userId: string,
        podId: string,
        resourcePath: string,
        content?: string | Blob,
        contentType?: string
    ) => Promise<Resource>;
    append: (userId: string, podId: string, resourcePath: string, content: string) => Promise<Resource>;
    deleteResource: (userId: string, podId: string, resourcePath: string) => Promise<void>;
}

// exclude non primitive types from query, to accomodate for a HTTP GET -based implementation
// if query on pods or identities is required, re-devise the API UsersQuery type
export type UsersQuery = Omit<Partial<UserDetails>, 'identities' | 'pods'>;

export interface IUsersMgmtServices {
    // Not made to be exposed in HTTP services, no need to define initPod in the interface
    // initPod: (podDescriptor: PodDescriptor, userId: string) => Promise<void>;

    /**
     * Creates a new user for the provided authentication information.
     * If a user already exists with these authentication info, an exception is thrown.
     * @param authenticationInfo
     */
    createUser: (authenticationInfo: AuthenticationInfo<POD_IDENTITY_PROVIDER>) => Promise<UserDetails>;

    /**
     * Find users.
     * Currently only supports query by hash (as computed with {@link computeUserHash}), and returns zero or one result.
     * @param userHash
     */
    getUsers(query: UsersQuery): Promise<UserDetails[]>;

    /**
     * Looks up a user by its identity
     * @param authenticationInfo
     */
    getUserByIdentity: (
        identity: Pick<Identity<POD_IDENTITY_PROVIDER>, 'id' | 'provider' | 'issuerUri'>
    ) => Promise<UserDetails | undefined>;

    /**
     * Removes a user from the platform entirely, inluding its tokens, consents and the content of its master pod
     * .datavillage directory.
     * @param userId
     */
    deleteUser: (userId: string) => Promise<void>;

    /**
     * Add a pod to an existing user
     * @param userId
     * @param pod
     * @param tokens
     */
    addPod: (
        userId: string,
        pod: PodDescriptor,
        tokens: {
            [key: string]: string;
        }
    ) => Promise<PodDescriptor>;

    /**
     * Check that the user account contains valid public and private keys, and that the tokens are valid against these, too.
     * @param userId
     * @param fix
     */
    checkIntegrity(userId: string, fix?: boolean): Promise<{ status: 'OK' | 'FIXED' | 'NOK'; message?: string }>;
}

export interface IUsersServices extends IBasicUsersServices {
    /**
     * Return an actionable instance of Pod for the given userId and podId
     * @param userId
     * @param podId
     */
    getPodInstance<T extends typeof MASTER_POD_ALIAS | string>(
        userId: string,
        podId: T
    ): T extends typeof MASTER_POD_ALIAS ? MasterPod : Pod;
}

/**
 * Decorator that takes a generic instance of BasicUsersServices, and implements the extra UsersServices methods on top
 */
export class BasicServiceDecorator
    extends Decorator<IBasicUsersServices>(
        'deleteResource',
        'saveResource',
        'getResource',
        'getResourceMetadata',
        'getPods',
        'getUser',
        'append'
    )
    implements IUsersServices
{
    getPodInstance<T extends typeof MASTER_POD_ALIAS | string>(
        userId: string,
        podId: T
    ): T extends typeof MASTER_POD_ALIAS ? MasterPod : Pod {
        // TODO check somehow that the returned pod is really the master pod ?
        //      or is sending the MASTER_POD_ALIAS as id enough of an insurance ?
        const result = podId == MASTER_POD_ALIAS ? new MasterPodWrapper(this, userId, podId) : (new PodWrapper(this, userId, podId) as Pod);

        return result as T extends typeof MASTER_POD_ALIAS ? MasterPod : Pod;
    }
}

/**
 * Implementation of the Pod interface that relies on a BasicUsersServices instance
 */
class PodWrapper implements Pod {
    private userServices: IBasicUsersServices;
    private userId: string;
    private podId: string;

    constructor(userServices: IBasicUsersServices, userId: string, podId: string) {
        this.userServices = userServices;
        this.userId = userId;
        this.podId = podId;
    }

    getResource(resourcePath: string): Promise<Resource> {
        return this.userServices.getResource(this.userId, this.podId, resourcePath);
    }

    getResourceMetadata(resourcePath: string): Promise<ResourceMetadata> {
        return this.userServices.getResourceMetadata(this.userId, this.podId, resourcePath);
    }

    getFile(resourcePath: string): Promise<FileResource> {
        return this.userServices.getResource(this.userId, this.podId, sanitizePath(resourcePath, 'NOSLASH')).then(res => {
            // throw exception if this is not a file
            assertResourceType(res, RESOURCE_TYPE.File);
            return res;
        });
    }

    async getFolder(resourcePath: string, create?: boolean): Promise<FolderResource> {
        // make sure the path ends with a slash
        const path = sanitizePath(resourcePath, 'SLASH');

        const folder = await this.userServices.getResource(this.userId, this.podId, path).catch(_404_undefined);

        if (folder) {
            // throw exception if this is not a folder
            assertResourceType(folder, RESOURCE_TYPE.Folder);

            return folder as FolderResource;
        } else if (create) {
            return this.userServices.saveResource(
                this.userId,
                this.podId,
                path,
                undefined,
                HTTP_FOLDER_CONTENTTYPE
            ) as Promise<FolderResource>;
        } else {
            throw new HttpError(404, 'Folder not found: ' + resourcePath);
        }
    }

    saveFile(filePath: string, content: string | Blob, contentType?: string): Promise<FileResource> {
        // make sure the path does not end with a slash (just in case)
        if (filePath.endsWith('/')) throw new Error('File name cannot end with a slash');

        return this.userServices.saveResource(this.userId, this.podId, filePath, content, contentType) as Promise<FileResource>;
    }

    saveResource(resourcePath: string, content?: string | Blob, contentType?: string): Promise<Resource> {
        return this.userServices.saveResource(this.userId, this.podId, resourcePath, content, contentType) as Promise<FileResource>;
    }

    append(filePath: string, content: string): Promise<FileResource> {
        return this.userServices.append(this.userId, this.podId, filePath, content) as Promise<FileResource>;
    }

    // TODO declare content as string | Blob
    deleteResource(path: string): Promise<void> {
        return this.userServices.deleteResource(this.userId, this.podId, path);
    }
}

class MasterPodWrapper extends PodWrapper implements MasterPod {
    constructor(userServices: IBasicUsersServices, userId: string, podId: string) {
        super(userServices, userId, podId);
    }

    getSecret(resourceId: string): Promise<string> {
        return this.getFile(`.datavillage/TOKENS/${encodeURIComponent(resourceId)}.key`).then(file => file.content.text());
    }

    storeSecret(resourceId: string, token: string) {
        return this.saveFile(`.datavillage/TOKENS/${encodeURIComponent(resourceId)}.key`, token).then(file => undefined);
    }

    getPublicKey(): Promise<string> {
        return this.getFile('.datavillage/personal-public.key').then(file => file.content.text());
    }

    async activateDatasource(
        sourceId: string,
        token: string,
        tokenScope: string[],
        userInfo: ProviderUserInfo,
        overwriteToken?: boolean,
        alreadyEncrypted?: boolean
    ): Promise<void> {
        const datasources = await this.getActivatedDatasources();

        // check if datasource is already present
        const existingDs = datasources.find(ds => ds.resourceUri == sourceId);
        if (existingDs && !overwriteToken) {
            // TODO throw exception, overwrite token ?
            throw new Error('Datasource already activated');
        } else {
            const newDs: DatasourceRecord = {
                activationDate: new Date().toISOString(),
                resourceUri: sourceId,
                userInfo,
                grantedScopes: tokenScope
            };

            // if overwriting an existing source, do not push a new object
            if (existingDs) Object.assign(existingDs, newDs);
            else datasources.push(newDs);

            await this.saveFile('.datavillage/datasources.json', JSON.stringify(datasources, undefined, 4));

            const encryptedToken = alreadyEncrypted ? token : _encryptStringWithRsaPublicKey(token, await this.getPublicKey());

            await this.storeSecret(sourceId, encryptedToken);
        }
    }

    async updateDatasourceStatistics(sourceUri: string, statistics: HarvestStatistics): Promise<void> {
        const datasources = await this.getActivatedDatasources();

        // TODO
        const datasource = datasources.find(ds => ds.resourceUri == sourceUri);
        if (datasource) {
            if (!datasource.statistics) datasource.statistics = [];
            datasource.statistics.unshift(statistics);

            // keep at most the 5 last entries
            datasource.statistics.length = Math.min(datasource.statistics.length, 5);

            await this.saveFile('.datavillage/datasources.json', JSON.stringify(datasources, undefined, 4));
        }
    }

    async getActivatedDatasources(): Promise<DatasourceRecord[]> {
        const datasources = await this.getFile('.datavillage/datasources.json')
            .then(file => file.content.text())
            .then(str => JSON.parse(str))
            .catch(_404_undefined);

        //TODO check data type ?

        return datasources || [];
    }

    async revokeDatasource(sourceId: string): Promise<void> {
        const datasources = await this.getActivatedDatasources();

        // check if datasource exists
        const idx = datasources.findIndex(ds => ds.resourceUri == sourceId);
        if (idx >= 0) {
            datasources.splice(idx, 1);
            await this.saveFile('.datavillage/datasources.json', JSON.stringify(datasources, undefined, 4));

            // let's ignore if the file is not found - weird, but it's gone and that's all that matters
            await this._deleteSecret(sourceId).catch(_404_undefined);
        } else {
            // TODO throw ?
        }
    }

    async updateApplicationLog(appId: string, summary: ApplicationProcessSummary): Promise<void> {
        const applications = await this.getActiveApplications();

        let application = applications.find(app => app.id == appId);

        // create app entry if needed
        if (!application) {
            applications.push((application = { id: appId }));
        }

        if (!application.recentActivity) application.recentActivity = [];
        application.recentActivity.unshift(summary);

        // keep at most the 5 last entries
        application.recentActivity.length = Math.min(application.recentActivity.length, 5);

        await this.saveFile('.datavillage/applications.json', JSON.stringify(applications, undefined, 4));
    }

    async getActiveApplications(): Promise<ApplicationDescriptor[]> {
        const applications = await this.getFile('.datavillage/applications.json')
            .then(file => file.content.text())
            .then(str => JSON.parse(str))
            .catch(_404_undefined);

        //TODO check data type ?

        return applications || [];
    }

    /**
     * For internal purposes
     * @param resourceId
     */
    _deleteSecret(resourceId: string) {
        return this.deleteResource(`.datavillage/TOKENS/${encodeURIComponent(resourceId)}.key`).then(file => undefined);
    }
}

export function getMasterPod(user: UserDetails): PodDescriptor {
    const masterPod = user.pods && user.pods.find(pod => pod.uri === user.masterPodUri);
    assert(masterPod, 'no master pod found for ' + user.id);

    return masterPod;
}

/**
 * Computes identity hash as it is used in the user DB
 * @param identity
 */
export function computeIdentityHash(
    identity: Pick<Identity<USER_IDENTITY_PROVIDER>, 'id' | 'provider' | 'issuerUri'> | { id: string; issuerUri: string }
): string {
    // if there's not issuerUri, pick the provider type as issuerUri (can't happen for Solid, will happen for Google)
    const issuerUri = 'provider' in identity ? identity.issuerUri || identity.provider : identity.issuerUri;
    assert(issuerUri, "Can't compute hash, missing issuerUri or provider");

    return crypto
        .createHash('md5')
        .update(JSON.stringify([identity.id, issuerUri]))
        .digest('hex');
}

