export function resourceMetadataToHeaders(resourceMetadata: ResourceMetadata) {
    const headers = {
        'Last-modified': resourceMetadata.lastModified
    };

    if (resourceMetadata.type == RESOURCE_TYPE.Folder) {
        headers['Content-Type'] = HTTP_FOLDER_CONTENTTYPE;
    } else {
        headers['Content-Type'] = resourceMetadata.contentType;
    }

    return headers;
}
