import { HttpClient } from '@datavillage-me/http-util';

import { HttpServiceClient } from '../http-client';
import { IBasicUsersServices, HTTP_FOLDER_CONTENTTYPE } from '../../core';
import { UserDetails } from '../../types';
import { assert } from '@datavillage-me/util';
import { PodDescriptor } from "../../types/authentication";
import { FileResource, FolderResource, Resource, RESOURCE_TYPE, ResourceMetadata } from "../../types/pod";

// subset the Http Response to the strictly needed fields, to ease the mocking in AWS Lambda bridge
export type HttpResponseSubset = Pick<Response, 'headers' | 'url' | 'blob' | 'json' | 'status' | 'statusText'>;

export function responseToResourceMetadata(response: HttpResponseSubset): ResourceMetadata {
    const lastModified = response.headers.get('last-modified') || '';

    // first check the custom DV header
    const contentType = response.headers.get('content-type') || undefined;
    // let's remove the charset if any
    const strictContentType = contentType && contentType.split(';')[0].trim();

    let type = strictContentType == HTTP_FOLDER_CONTENTTYPE ? RESOURCE_TYPE.Folder : RESOURCE_TYPE.File;
    if (response.url.endsWith('/') && type != RESOURCE_TYPE.Folder) {
        throw new Error('Content-type mismatch : resource with trailing slash is not advertised as folder: ' + response.url);
    }

    // get the path elements, excluding the slashes
    const tokens = new URL(response.url).pathname.split('/').filter(elem => elem);

    let name = tokens.length > 0 ? tokens[tokens.length - 1] : '';
    if (type == RESOURCE_TYPE.Folder) name += '/';

    assert(name, 'Unable to get resource name: ' + response.url);

    return {
        lastModified,
        name,
        type,
        contentType
    };
}

export async function responseToResource(response: HttpResponseSubset): Promise<Resource> {
    const metadata = responseToResourceMetadata(response);
    if (metadata.type == RESOURCE_TYPE.Folder) {
        return {
            metadata,
            content: await response.json()
        } as FolderResource;
    } else {
        // TODO try to parse as Text only if the Content-Type hints at that
        //      for all we know, it's just a bytestream
        return {
            metadata,
            content: await response.blob()
        } as FileResource;
    }
}

export class BasicHttpUsersServices extends HttpServiceClient implements IBasicUsersServices {
    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    getUser(userId: string): Promise<UserDetails> {
        return this.httpClient.getJson(`/users/${userId}`);
    }

    getPods(userId: string): Promise<PodDescriptor[]> {
        return this.httpClient.getJson(`/users/${userId}/pods`);
    }

    getPod(userId: string, podId: string): Promise<PodDescriptor> {
        return this.httpClient.getJson(`/users/${userId}/pods/${podId}`);
    }

    async getResource(userId: string, podId: string, resourcePath: string): Promise<Resource> {
        const response = await this.httpClient.authorizedFetch(
            `/users/${userId}/pods/${encodeURIComponent(podId)}/resources/${resourcePath}`
        );
        return responseToResource(response);
    }

    async getResourceMetadata(userId: string, podId: string, resourcePath: string): Promise<ResourceMetadata> {
        const response = await this.httpClient.head(`/users/${userId}/pods/${podId}/resources/${resourcePath}`);
        return responseToResourceMetadata(response);
    }

    saveResource(userId: string, podId: string, resourcePath: string, resourceContent?: string | Blob, contentType?: string): Promise<Resource> {
        // pro-actively add the folder content-type if we have a trailing slash
        if (!contentType && resourcePath.endsWith('/')) contentType = HTTP_FOLDER_CONTENTTYPE;

        const headers = contentType ? { 'Content-Type': contentType } : undefined;

        return this.httpClient
            .authorizedFetch(`/users/${userId}/pods/${podId}/resources/${resourcePath}`, {
                headers,
                method: 'PUT',
                body: resourceContent
            })
            .then(responseToResource);
    }

    append(userId: string, podId: string, resourcePath: string, appendedContent: string): Promise<Resource> {
        // make sure the path does not end with a slash (just in case)
        if (resourcePath.endsWith('/')) throw new Error('File name cannot end with a slash');

        return this.httpClient
            .authorizedFetch(`/users/${userId}/pods/${podId}/resources/${resourcePath}`, {
                method: 'POST',
                body: appendedContent
            })
            .then(responseToResource);
    }

    deleteResource(userId: string, podId: string, resourcePath: string): Promise<void> {
        return this.httpClient.delete(`/users/${userId}/pods/${podId}/resources/${resourcePath}`);
    }
}
