import { HttpClient, initSearchParams } from '@datavillage-me/http-util';

import { HttpServiceClient } from '../http-client';
import { IUsersMgmtServices, UsersQuery } from '../../core';
import { UserDetails} from '../../types';
import { AuthenticationInfo, Identity, POD_IDENTITY_PROVIDER, PodDescriptor } from "../../types/authentication";

export class HttpUsersMgmtServices extends HttpServiceClient implements IUsersMgmtServices {
    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    createUser(authenticationInfo: AuthenticationInfo<POD_IDENTITY_PROVIDER>): Promise<UserDetails> {
        // TODO JSON.stringify remove potentially?
        return this.httpClient.postJson(`/users`, authenticationInfo);
    }

    getUsers(query: UsersQuery): Promise<UserDetails[]> {
        return this.httpClient.getJson(`/users?${initSearchParams(query).toString()}`);
    }

    addPod(userId: string, pod: PodDescriptor, tokens: { [p: string]: string }): Promise<PodDescriptor> {
        throw new Error('Not implemented');
    }

    deleteUser(userId: string): Promise<void> {
        return this.httpClient.delete(`/users/${userId}`);
    }

    checkIntegrity(userId: string, fix?: boolean): Promise<{ status: 'OK' | 'FIXED' | 'NOK'; message?: string }> {
        return this.httpClient.postJson(`/users/${userId}/checkIntegrity`, undefined, { query: { fix } });
    }

    getUserByIdentity(identity: Pick<Identity<POD_IDENTITY_PROVIDER>, 'id' | 'provider' | 'issuerUri'>): Promise<UserDetails | undefined> {
        return this.httpClient.getJson<UserDetails[]>(`/users`, { query: identity }).then(users => users[0]);
    }
}
