import { appendQueryParameters, HttpClient } from '@datavillage-me/http-util';

import { CONNECT_ACTION, Credentials, DEV_CONNECT_ACTION, Passport } from "../../core";
import { HttpServiceClient } from '../http-client';
import { Developer, UserDetails } from '../../types';
import { DATASOURCE_IDENTITY_PROVIDER, DEVELOPER_IDENTITY_PROVIDER, POD_IDENTITY_PROVIDER } from "../../types/authentication";

export class HttpPassport extends HttpServiceClient implements Passport {
    constructor(httpClient: HttpClient) {
        super(httpClient);
    }

    async getAuthenticationUrl(activationCallbackUrl: string, type?: POD_IDENTITY_PROVIDER, issuer?: string): Promise<string> {
        if (type) {
            const url = new URL(this.httpClient.getUrl('/auth/connect/' + type));
            url.search = new URLSearchParams({
                activationCallbackUrl,
                action: CONNECT_ACTION.ADD_POD
            }).toString();
            issuer && url.searchParams.append('issuer', issuer);

            return url.toString();
        } else {
            // Currently support a generic login page in case of missing type
            const url = new URL(this.httpClient.getUrl('/auth/login'));
            url.search = new URLSearchParams({
                redirectUrl: activationCallbackUrl
            }).toString();

            return url.toString();
        }
    }

    async getDeveloperToken(activationCallbackUrl: string, type: DEVELOPER_IDENTITY_PROVIDER, developerId: string): Promise<string> {
        const url = new URL(this.httpClient.getUrl('/auth/dev/connect/' + type));
        url.search = new URLSearchParams({
            activationCallbackUrl,
            action: DEV_CONNECT_ACTION.REQUEST_DEVELOPER_TOKEN,
            developerId
        }).toString();
        return url.toString();
    }

    async getDatasourceActivationUrl(activationCallbackUrl: string, type: DATASOURCE_IDENTITY_PROVIDER, force?: boolean): Promise<string> {
        const url = appendQueryParameters(this.httpClient.getUrl('/auth/connect/' + type), {
            activationCallbackUrl,
            action: CONNECT_ACTION.CONNECT_SOURCE,
            force
        });

        return url.toString();
    }

    authenticate(user: string, password: string): Promise<UserDetails> {
        throw new Error('Not implemented');
    }

    logout(): Promise<void> {
        return this.httpClient.post('/auth/logout', undefined).then(value => undefined);
    }

    getCurrentUser(): Promise<UserDetails | undefined> {
        const response: Promise<UserDetails> = this.httpClient.getJson('/auth/me', {
            //mode: 'cors',
        });
        return response.catch(err => {
            if (err.status == 401) return undefined;
            else throw err;
        });
    }

    getCurrentDeveloper(): Promise<Developer | undefined> {
        const response: Promise<Developer> = this.httpClient.getJson('/auth/dev/me', {
            //mode: 'cors',
        });
        return response.catch(err => {
            if (err.status == 401) return undefined;
            else throw err;
        });
    }

    getCurrentCredentials(): Promise<Credentials | undefined> {
        const response: Promise<Credentials> = this.httpClient.getJson('/auth/currentCredentials', {
            //mode: 'cors',
        });
        return response.catch(err => {
            if (err.status == 401) return undefined;
            else throw err;
        });
    }

    developerLogout(): Promise<void> {
        return this.httpClient.post('/auth/dev/logout', undefined).then(value => {});
    }

    createUserToken(userId: string, scope: string): Promise<{ access_token: string; refresh_token: string }> {
        return this.httpClient.postJson(
            '/auth/createToken',
            {
                userId,
                scope
            },
            { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }
        );
    }

    createDevToken(devId: string, scope: string): Promise<{ access_token: string; refresh_token: string }> {
        return this.httpClient.postJson(
          '/auth/dev/createToken',
          {
              devId,
              scope
          },
          { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }
        );
    }

    activateSources(consentReceiptId: string): Promise<void> {
        return this.httpClient.postJson(
            '/sources/activate',
            {
                consentReceiptId
            },
            { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }
        );
    }
}
