import { Developer, UserDetails } from '../types';
import { DATASOURCE_IDENTITY_PROVIDER, DEVELOPER_IDENTITY_PROVIDER, POD_IDENTITY_PROVIDER } from '../types/authentication';

export enum CONNECT_ACTION {
    ADD_POD = 'AddPod',
    CONNECT_SOURCE = 'ConnectSource'
}

export enum DEV_CONNECT_ACTION {
    DEVELOPER_AUTHENTICATION = 'DeveloperAuthentication',
    REQUEST_DEVELOPER_TOKEN = 'REQUEST_DEVELOPER_TOKEN'
}

export type Credentials = {
    userId: string | undefined;
    role: string | undefined;
    developerId: string | undefined;
    spaceId: string | undefined;
    roles: string[];
    clientId: string | undefined;
    isPlatformAdmin: boolean | undefined;
};

/**
 * Interface to the Passport functionalities.
 * The Passport is the main entry point for external application, to perform login, registration, ...
 */
export interface Passport {
    /**
     * Obtain a URL to the passport endpoint that will start the login flow for the given pod type.
     * The flow will redirect the browser to the appropriate page for the given pod type and issuer, and process the
     * resulting authentication tokens to produce an authentication cookie valid for the passport API.
     * For Solid authentication, is is recommended to provide the identity issue. If not, the passport backend will use
     * a default one, like http://inrupt.net .
     *
     * @param type the type of identity provider
     * @param activationCallbackUrl where to redirect the browser after the full passport login process
     * @param issuer the specific identity provider (mostly for Solid)
     */
    getAuthenticationUrl: (activationCallbackUrl: string, type?: POD_IDENTITY_PROVIDER, issuer?: string) => Promise<string>;

    /**
     * Obtain a developer token
     * The flow will redirect the browser to the appropriate page for the given pod type and issuer,
     * and return a postMessage containing the token
     *
     * @param type the type of identity provider
     * @param activationCallbackUrl where to redirect the browser after the full passport login process
     * @param developerId the id of the developer for which a token has to be generated
     */
    getDeveloperToken: (activationCallbackUrl: string, type: DEVELOPER_IDENTITY_PROVIDER, developerId: string) => Promise<string>;

    /**
     * Obtain a URL to the passport endpoint that will start the login flow for the given datasource.
     * The flow will redirect the browser to the appropriate page for the given datasource, and process the
     * resulting authentication to activate and register the datasource in the Datavillage user account.
     *
     * @param type the type of datasource
     * @param activationCallbackUrl where to redirect the browser after the full passport login process
     */
    getDatasourceActivationUrl: (activationCallbackUrl: string, type: DATASOURCE_IDENTITY_PROVIDER, force?: boolean) => Promise<string>;

    /**
     * Perform a direct authentication using a basic user/password scheme.
     * @param user
     * @param password
     */
    authenticate: (user: string, password: string) => Promise<UserDetails>;

    /**
     * Log out of the passport API.
     */
    logout: () => Promise<void>;

    /**
     * Retrieve the current user authenticated with the Passport API.
     */
    getCurrentUser: () => Promise<UserDetails | undefined>;

    /**
     * Retrieve the current developer authenticated with the Passport API.
     */
    getCurrentDeveloper: () => Promise<Developer | undefined>;

    getCurrentCredentials: () => Promise<Credentials | undefined>;

    developerLogout: () => Promise<void>;

    activateSources: (consentId: string) => Promise<void>;

    createUserToken: (userId: string, scope: string) => Promise<{ access_token: string; refresh_token: string }>;

    createDevToken: (devId: string, scope: string) => Promise<{ access_token: string; refresh_token: string }>;
}
