import { assert } from '@datavillage-me/util';
import {
    ApplicationDescriptor,
    ApplicationProcessSummary,
    DatasourceRecord,
    HarvestStatistics,
    ProviderUserInfo
} from "./dataSources";

export enum RESOURCE_TYPE {
    Folder = 'folder',
    File = 'file'
}

export function assertResourceType<T extends RESOURCE_TYPE>(resource: Resource, type: T): asserts resource is Resource<T> {
    assert(resource.metadata.type == type, `Resource is not a ${type} : ${resource.metadata.name}`);
}

export interface ResourceMetadata<T extends RESOURCE_TYPE = RESOURCE_TYPE> {
    lastModified: string;
    name: string;
    type: T;
    directUrl?: string;
    contentType?: string;
}

// TODO refactor Resource model to have content out of abstract Resource and children instead of content in FolderResource
export interface Resource<T extends RESOURCE_TYPE = RESOURCE_TYPE> {
    metadata: ResourceMetadata<T>;
    content: any;
}

export interface FolderResource extends Resource<RESOURCE_TYPE.Folder> {
    // list of folder-relative paths to its sub resources
    // TODO make sure it is possible to retrieve complete RDF metadata from HEAD Folder in Solid client
    content: ResourceMetadata[];
}

// TODO in the future, support stream-based content
export interface FileResource extends Resource<RESOURCE_TYPE.File> {
    content: Blob;
}


/**
 * Access point to a user's pod.
 * Embeds authentication information, and exposes easy file/folder operations.
 */
export interface Pod {
    /**
     * Fetch a resource
     * @param resourcePath
     */
    getResource(resourcePath: string): Promise<Resource>;

    /**
     * Get a resource metadata, without fetching content
     * @param resourcePath
     */
    getResourceMetadata(resourcePath: string): Promise<ResourceMetadata>;

    /**
     * Fetch a file
     * @param resourcePath
     */
    getFile(resourcePath: string): Promise<FileResource>;

    /**
     * Fetch a folder, and optionally create it on the fly
     * @param resourcePath
     */
    getFolder(resourcePath: string, create?: boolean): Promise<FolderResource>;

    /**
     * Save a file in the pod.
     * @param resourcePath
     * @param content
     * @param contentType
     */
    saveFile(resourcePath: string, content?: string | Blob, contentType?: string): Promise<FileResource>;

    /**
     * Save a resource (file or folder) in the pod.
     * If the path ends with a slash, or the mimetype is HTTP_FOLDER_CONTENTTYPE, the content must be empty and the resource will be considered a folder and created if necessary.
     * @param resourcePath
     * @param content
     * @param contentType
     */
    saveResource(resourcePath: string, content?: string | Blob, contentType?: string): Promise<Resource>

    /**
     * Append content to a file
     * @param resourcePath
     */
    append(resourcePath: string, content: string): Promise<FileResource>;

    /**
     * Delete a resource (file or folder), and all its content
     * @param resourcePath
     */
    deleteResource(resourcePath: string): Promise<void>;
}



export interface ExternalPod extends Pod {
    /**
     * Returns the URI that identifies this pod
     */
    getUri(): string;
}



// TODO should the MasterPod interface be advertised in the public API ?
//      these operations are for DV only.
//      Meddling with the master pod is technically feasible anyway for the user (he's in command of his pod),
//      but advertising this API may be an incitation to misusage

/**
 * Extended set of operations only available on the master pod.
 * These operations should not be carried out by or on behalf of the user - they are meant for internal Datavillage processes.
 */
export interface MasterPod extends Pod {
    /**
     * Returns the secret (encrypted) token for the given resource.
     * @param resourceUri
     */
    getSecret(resourceUri: string): Promise<string>;

    /**
     * Store a resource secret token in the TOKENS folder.
     * WARN : token is stored as is in the pod. It is expected to be encrypted with the user public key.
     * @param resourceUri
     * @param token
     */
    storeSecret(resourceUri: string, token: string): Promise<void>;

    getPublicKey(): Promise<string>;

    getActivatedDatasources(): Promise<DatasourceRecord[]>;

    getActiveApplications(): Promise<ApplicationDescriptor[]>;

    /**
     * Remove a datasource from the activated sources.
     * That means also removing the authentication token from the user's pod.
     * @param sourceUri
     */
    revokeDatasource(sourceUri: string): Promise<void>;

    /**
     * Activate a datasource.
     * Token will be encrypted using the master pod public key, unless alreadyEncrypted is true, and stored in the pod.
     * @param resourceUri
     * @param token
     * @param userInfo the user metadata corresponding to the provider token being registered
     * @param alreadyEncrypted if true, token will not be encrypted before storage
     */
    activateDatasource(
      sourceUri: string,
      token: string,
      tokenScope: string[],
      userInfo: ProviderUserInfo,
      overwriteToken?: boolean,
      alreadyEncrypted?: boolean
    ): Promise<void>;

    /**
     * Update datasource statistics.
     */
    updateDatasourceStatistics(sourceUri: string, statistics: HarvestStatistics): Promise<void>;

    /**
     * Update application activity log.
     */
    updateApplicationLog(appId: string, summary: ApplicationProcessSummary): Promise<void>;
}
