import { ImportDataParams } from '../core';

/**
 * for testing purposes
 */
export declare type TestJobRequest = {
    type: 'TEST';
    params: {
        duration: number;
    };
    /** delay in seconds before the job can be processed */
    delay?: number;
};

/**
 * Job request to do a datasource import
 */
export declare type ImportDataJobRequest = {
    type: 'IMPORT_DATA';
    params: {
        /** The user to import data for */
        userId: string;
        /** The datasource to import data from */
        datasourceId: string;
        /** import job params */
        importParams?: ImportDataParams;
    };
    /** delay in seconds before the job can be processed */
    delay?: number;
};

/**
 * Job request to start a data inference process
 */
export declare type InferEngineJobRequest = {
    type: 'INFER' | 'LOAD_MEDIA' | 'RESET';
    params: {
        /** The users fow which to do the processing */
        userIds?: string[];
        /** The client */
        clientId: string;
        /** The application */
        applicationId: string;
    };
    /** delay in seconds before the job can be processed */
    delay?: number;
};

export type JobRequest = TestJobRequest | ImportDataJobRequest | InferEngineJobRequest;

export type JOB_TYPES = JobRequest['type'];

/**
 * Possible status for a job
 */
export enum JobStatus {
    'ERROR' = 'ERROR',
    'PENDING' = 'PENDING',
    'RUNNING' = 'RUNNING',
    'SUCCESS' = 'SUCCESS'
}

export type JobCurrentState = {
    status: JobStatus;
    message: string;
    timestamp: number;
    progress: number;
};

/**
 * The model of a Job instance
 */
export type Job = {
    /**
     * unique ID for the job
     */
    id: string;

    /**
     * when the job was created
     */
    creationTime: number;

    /**
     * when the job was started
     */
    startTime?: number;

    /**
     * when the job was ended
     */
    endTime?: number;

    /**
     * the user account subject to this activities
     */
    userId?: string;

    /**
     * informational message
     */
    message: string;

    /**
     * optional result object, containing job summary
     */
    // TODO strong type this per JobRequest type
    result?: any;

    /**
     * The consent in the scope of which the job takes place (optional)
     */
    consentId?: string;

    /**
     * The job status
     */
    status: JobStatus;

    /**
     * The job progress status. 0 if PENDING, 1 if SUCCESS or ERROR, anything between 0 and 1 if RUNNING.
     */
    progress: number;

    /**
     * The original request that contains the job params
     */
    request: JobRequest;

    /**
     * History of the job status messages
     */
    history: {
        timestamp: number;
        status?: JobStatus;
        message?: string;
        progress?: number;
    }[];
};

export type JobEvent = {
    jobId: string;
};
