import { IConsentReceiptsServices, UnprefixedForPersonalData } from '../../core';
import { HttpServiceClient } from '../http-client';
import { ConsentReceipt, DataProcessing } from '../../types';

export class HttpConsentReceiptsServices extends HttpServiceClient implements IConsentReceiptsServices {
    getConsentReceiptUrl(consentReceiptId: string): string {
        return this.httpClient.getUrl(`/consentReceipts/${consentReceiptId}`)
    }

    getConsentReceipt(consentReceiptId: string): Promise<ConsentReceipt | undefined> {
        return this.httpClient.getJson(`/consentReceipts/${consentReceiptId}`);
    }

    getCurrentConsentReceiptForApplication(clientId: string, applicationId: string): Promise<ConsentReceipt | undefined> {
        return this.httpClient.getJson(`/clients/${clientId}/spaces/${applicationId}/currentConsentReceipt`);
    }

    createConsentReceipt(
        clientId: string,
        applicationId: string,
        purpose: string,
        forProcessing: DataProcessing[],
        forPersonalData: UnprefixedForPersonalData[]
    ): Promise<ConsentReceipt> {
        return this.httpClient.postJson(`/clients/${clientId}/spaces/${applicationId}/consentReceipts`, {
            purpose,
            forProcessing,
            forPersonalData
        });
    }
}
