type AddUpdateConsentParams = {
    applicationId: ConsentReceipt['applicationId'];
    consentId: Consent['@id'];
    consentReceiptId: ConsentReceipt['@id'];
    previousConsentId?: Consent['@id'];
    forPersonalData: Consent['gConsent:forPersonalData'];
    duration: number;
};

type KnownActivity = AbstractActivity &
    (
        | { type: 'DATASOURCE_REVOKE' }
        | { type: 'READ_POD_DATA' }
        | { type: 'WRITE_POD_DATA' }
        | {
              type: 'WRITE_APP_RESULT';
              params: {
                  podResources: string[];
                  applicationId: string;
                  //consentId: string;
                  //clientId: string;
              };
          }
        | {
              type: 'INIT_POD';
              params: {
                  podUri: string;
              };
          }
        | { type: 'ISSUE_TOKEN'; params: { scope: string } }
        | { type: 'ADD_CONSENT'; params: AddUpdateConsentParams }
        | { type: 'UPDATE_CONSENT'; params: AddUpdateConsentParams }
        | {
              type: 'DATA_IMPORT';
              params: ImportDataParams & { datasource: string };
          }
        | {
              type: 'REVOKE_CONSENT';
              params: {
                  applicationId: string;
                  consentId: string;
                  consentReceiptId: string;
              };
          }
        | {
              type: 'DATA_PROCESSING';
              params: {
                  applicationId: string;
                  consentId: string;
                  sources: { datasourceId: string; mappingId: string; label: string }[];
              };
          }
        | {
              type: 'DATASOURCE_GRANT';
              params: { datasource: string };
          }
        | {
              // for backward compatibility, and
              type: undefined;
              message: string;
          }
    );

export type ActivityType = KnownActivity['type'];

export type Activity<T extends ActivityType = ActivityType> = KnownActivity & {
    type: T;
};

/**
 * The severity levels of Activity Log entries
 */
export enum ActivityLogLevel {
    'ERROR',
    'WARNING',
    'INFO'
}

