/**
 * The model of an Activity Log entry, as exposed by ActivityLogService
 */
export type AbstractActivity = {
    /**
     * when the event took place
     */
    timestamp: number;

    /**
     * the user account subject to this activity
     */
    userId: string;

    /**
     * The consent in the scope of which the activity takes place (optional)
     */
    consentId?: string;

    /**
     * The severity level
     */
    logLevel: ActivityLogLevel;
};
