class MockPassport implements Passport {
    private currentUser: UserDetails | undefined;

    constructor(currentUserId?: string) {
        this.currentUser = currentUserId ? USERS[currentUserId] : undefined;
    }

    async getAuthenticationUrl(activationCallbackUrl: string, type?: POD_IDENTITY_PROVIDER, issuer?: string): Promise<string> {
        const callbackURL = new URL(activationCallbackUrl);

        const mockLoginURL = new URL('https://dv-dev-mocked-content.s3.amazonaws.com/externalLoginMock.html');
        mockLoginURL.search = new URLSearchParams({
            activationCallbackUrl: callbackURL.toString(),
            users: Object.keys(USERS).join(',')
        }).toString();

        return mockLoginURL.toString();
    }

    async getDeveloperToken(activationCallbackUrl: string, type: DEVELOPER_IDENTITY_PROVIDER, developerId: string): Promise<string> {
        throw new Error('Not implemented');
    }

    async getDatasourceActivationUrl(activationCallbackUrl: string, type: DATASOURCE_IDENTITY_PROVIDER, force?: boolean): Promise<string> {
        // TODO add static page in https://dv-dev-mocked-content.s3.amazonaws.com

        throw new Error('Not implemented');
    }

    async authenticate(user: string, password: string): Promise<UserDetails> {
        if (USERS[user]) {
            this.currentUser = USERS[user];
            return this.currentUser;
        } else {
            throw new Error('Authentication failed');
        }
    }

    async logout(): Promise<void> {
        this.currentUser = undefined;
        return Promise.resolve(undefined);
    }

    async activateSources(consentId: string): Promise<void> {
        throw new Error('Not implemented');
    }

    getCurrentUser(): Promise<UserDetails | undefined> {
        return Promise.resolve(this.currentUser);
    }

    getCurrentDeveloper(): Promise<Developer | undefined> {
        throw 'Not implemented';
    }

    getCurrentCredentials(): Promise<Credentials | undefined> {
        throw 'Not implemented';
    }

    async developerLogout(): Promise<void> {
        throw 'Not implemented';
    }

    createDevToken(devId: string, scope: string): Promise<{ access_token: string; refresh_token: string }> {
        throw new Error('Not implemented');
    }

    createUserToken(userId: string, scope: string): Promise<{ access_token: string; refresh_token: string }> {
        throw new Error('Not implemented');
    }
}

const MockUsersServices: IUsersServices = new BasicServiceDecorator({
    getUser(userId: string): Promise<UserDetails> {
        return Promise.resolve(USERS[userId]);
    },

    getPods(userId: string): Promise<PodDescriptor[]> {
        return Promise.resolve(USERS[userId].pods);
    },

    getPod(userId: string, podId: string): Promise<PodDescriptor> {
        return Promise.resolve(USERS[userId].pods[0]);
    },

    async getResource(userId: string, podId: string, resourcePath: string): Promise<Resource> {
        const user = await this.getUser(userId);
        const pod = podId == MASTER_POD_ALIAS ? getMasterPod(user) : user.pods.find(value => value.uri == podId);

        assert(pod, 'Pod not found: ' + podId);

        const podRoot = PODS[pod.uri]!;

        const pathElements = sanitizePath(resourcePath, 'NOSLASH', 'RELATIVE').split('/');

        let result = pathElements.reduce<MockedFolderResource | MockedFileResource>((previousValue, currentValue, idx) => {
            if (currentValue == '') {
                if (idx > 0) throw new Error('Wrong path : ' + resourcePath);
                // the first token is the empty string - return the root
                else return previousValue;
            }

            if (previousValue.metadata.type != RESOURCE_TYPE.Folder) throw new Error('Not a folder : ' + previousValue.metadata.name);

            const result = (previousValue as MockedFolderResource).content.find(value => value.metadata.name == currentValue);

            if (!result) throw new Error('Missing resource : ' + currentValue);

            return result;
        }, podRoot);

        if (result.metadata.type == RESOURCE_TYPE.Folder) {
            return { ...result, content: (result as MockedFolderResource).content.map(value => value.metadata) };
        } else {
            return result;
        }
    },

    getResourceMetadata(userId: string, podId: string, resourcePath: string): Promise<ResourceMetadata> {
        return this.getResource(userId, podId, resourcePath).then(value => value.metadata);
    },

    saveResource(
        userId: string,
        podId: string,
        resourcePath: string,
        resourceContent?: string | Blob,
        contentType?: string
    ): Promise<Resource> {
        throw new Error('Not implemented');
    },
    append(userId: string, podId: string, resourcePath: string, content: string): Promise<Resource> {
        throw new Error('Not implemented');
    },

    deleteResource(userId: string, podId: string, resourcePath: string): Promise<void> {
        throw new Error('Not implemented');
    }
});

