import { AUTH_MODE, HttpClient } from '@datavillage-me/http-util';

export class HttpServiceClient {
    private _httpClient: HttpClient;

    constructor(httpClient: HttpClient) {
        this._httpClient = httpClient;
    }

    static forUrl<T>(cons: new (httpClient: HttpClient) => T, api_url: string, auth_token?: string, authMode?: AUTH_MODE): T {
        return new cons(new HttpClient(api_url, auth_token, authMode));
    }

    get httpClient(): HttpClient {
        return this._httpClient;
    }
}
