export type EnvironmentSettings = Record<string, string>;

export type GeneralSettings = {
    name?: string;
    description?: string;
    version?: string;
    purpose?: string;
    benefit?: string;
    frequency?: string;
};

/**
 * These are the algo & cage settings as stored in the backend DB
 */
export type AlgoSettings = {
    algo: {
        enabled: boolean;
        cpu: number;
        memory: number;
        replicas: number;
        source?: {
            registry: string;
            image: string;
            tag: string;
            secret?: string;
            username?: string;
            password?: string;
            email?: string;
        };
        service?: boolean;
        category: string;
    };
    jupyter: {
        enabled: boolean;
        cpu?: number;
        memory?: number;
    };
    redis: {
        enabled: boolean;
        cpu?: number;
        memory?: number;
    };
    neo4j: {
        enabled: boolean;
        cpu?: number;
        memory?: number;
    };
    postgresql: {
        enabled: boolean;
        cpu?: number;
        memory?: number;
    };
    cronevents: { event: string; cron: string }[];
    environments: { name: string; value: string }[];
};

/**
 * This is the object model expected by the cage '/deploy' endpoint.
 * It extends the backend AlgoSettings with a some extra fields to be injected  on the fly
 * when invoking cage /deploy
 */
export type FullAlgoSettings = AlgoSettings & {
    dv_token: string;
    space_id: string;

    enclaving: {
        enabled: boolean;
    };
    sandboxing: {
        enabled: boolean;
        allowed: string[];
    };

    // TODO remove this once it is no longer needed by the cage
    client_id?: string;
    app_id?: string;
};

