import { CredentialManager, GetApplicationResponse } from '../../core';
import { HttpServiceClient } from '../http-client';

export class HttpCredentialManager extends HttpServiceClient implements CredentialManager {
    getApplicationToken(clientId: string, clientSecret: string): Promise<GetApplicationResponse> {
        return this.httpClient.postJson(
            '/auth/getApplicationToken',
            {
                client_id: clientId,
                client_secret: clientSecret
            },
            { headers: { 'Content-Type': 'application/json' } }
        );
    }
}
