import { Frame } from 'jsonld/jsonld-spec';
import { NodeObject } from 'jsonld';

export enum ConsentStatus {
    Given = 'gConsent:Given',
    Withdrawn = 'gConsent:Withdrawn'
}

export enum DataProcessing {
    Adaptation = 'gConsent:DataAdaptation',
    Alignment = 'gConsent:DataAlignment',
    Alteration = 'gConsent:DataAlteration',
    Collection = 'gConsent:DataCollection',
    Combination = 'gConsent:DataCombination',
    Consultation = 'gConsent:DataConsultation',
    Destruction = 'gConsent:DataDestruction',
    Disclosure = 'gConsent:DataDisclosure',
    Dissemination = 'gConsent:DataDissemination',
    Erasure = 'gConsent:DataErasure',
    Organisation = 'gConsent:DataOrganisation',
    Recording = 'gConsent:DataRecording',
    Restriction = 'gConsent:DataRestriction',
    Retrieval = 'gConsent:DataRetrieval',
    Sharing = 'gConsent:DataSharing',
    Storage = 'gConsent:DataStorage',
    Structuring = 'gConsent:DataStructuring',
    Use = 'gConsent:DataUse'
}

export enum Context {
    dev = 'https://api.datavillage.me/consent/context.jsonld',
    staging = 'https://api.datavillage.me/consent/context.jsonld'
}

// TODO use fully qualified URIs for actions and providers throughout the platform
//      at the moment the schema: and dv: namespaces are hardcoded in the RDF context
export type ForPersonalData = {
    /** list of actions expressed in the schema namespace, e.g. schema:LikeAction */
    'dvConsent:personalDataType': string;
    /** list of provider IDs expressed in the dv namespace, e.g. dv:spotify */
    'dvConsent:personalDataSource': string[];
};

export type ConsentReceipt = {
    '@context': Context;
    '@id': string;
    'gConsent:atTime': {
        'time:inXSDDateTimeStamp': string;
    };
    'gConsent:forPersonalData': ForPersonalData[];
    'gConsent:forProcessing': DataProcessing[];
    'gConsent:purpose': string;
    clientId: string;
    applicationId: string;
    previousConsentReceiptId?: string;
    // TODO signature
};

export type Consent = {
    '@context': Context;
    '@id': string;
    'gConsent:hasStatus': ConsentStatus;
    'gConsent:atTime': {
        'time:inXSDDateTimeStamp': string;
    };
    'gConsent:forPersonalData': ForPersonalData[];
    'gConsent:forProcessing': DataProcessing[];
    'gConsent:purpose': string;
    'gConsent:hasExpiry': {
        '@type': 'time:Interval';
        'time:hasEnd': {
            'time:inXSDDateTimeStamp': string;
        };
    };
    userId: string;
    clientId: string;
    applicationId: string;
    consentReceiptId: string;
    previousVersionTime?: {
        'time:inXSDDateTimeStamp': string;
    };

    type: 'https://w3id.org/GConsent#Consent';
    // TODO signature
};

export type ConsentHistory = {
    id: string;
    consentStatus: ConsentStatus;
    consentReceiptId: string;
    userId: string;
    clientId: string;
    applicationId: string;
    hasExpiry: string;
    history: Consent[];
};

export const consentContext_V1: NodeObject['@context'] = {
    '@base': 'https://api.datavillage.me/consents/',
    xsd: 'http://www.w3.org/2001/XMLSchema#',
    gConsent: 'https://w3id.org/GConsent#',
    dvConsent: 'https://datavillage.me/consent#',
    'time:inXSDDateTimeStamp': { '@type': 'xsd:dateTime' },
    type: '@type',
    'gConsent:forProcessing': { '@type': '@id', '@container': '@set' },
    'gConsent:hasStatus': { '@type': '@id' },
    'dvConsent:personalDataType': { '@type': '@id' },
    'dvConsent:personalDataSource': { '@container': '@set' },
    'gConsent:forPersonalData': {
        '@container': '@set'
    },
    'gConsent:hasExpiry': {
        '@type': 'time:Interval'
    }
    //"@id" : '@id'
};

export const consentFrame_V1: Frame = {
    '@context': consentContext_V1,
    '@explicit': true,
    '@requireAll': true,
    type: 'https://w3id.org/GConsent#Consent',

    'gConsent:hasStatus': {},
    'gConsent:atTime': {},
    'gConsent:forPersonalData': {
        'dvConsent:personalDataType': {},
        'dvConsent:personalDataSource': {}
    },
    'gConsent:forProcessing': {},
    'gConsent:purpose': {},
    'gConsent:hasExpiry': {
        'time:hasEnd': {
            'time:inXSDDateTimeStamp': {}
        }
    },
    id: {},
    userId: {},
    clientId: {},
    applicationId: {},
    consentReceiptId: {},
    previousVersionTime: {
        '@default': '@null',
        '@omitDefault': true,
        'time:inXSDDateTimeStamp': {}
    }
};
