import { HttpClient } from '@datavillage-me/http-util';
import { BasicHttpUsersServices } from './usersServices';
import { BasicServiceDecorator, IUsersServices } from '../../core';

// Declare the HttpUsersServices as a decorated BasicHttpUsersServices
// needs the 'any' casting trick, as per https://stackoverflow.com/questions/63494928/revealing-module-whose-target-lacks-a-construct-signature
// make sure to use a regular function, as arrow functions cannot be used as constructors
export const HttpUsersServices = function (httpClient: HttpClient) {
    return new BasicServiceDecorator(new BasicHttpUsersServices(httpClient));
} as any as {
    new (httpClient: HttpClient): IUsersServices;
};

export { HttpUsersMgmtServices } from './usersMgmtServices';
