export enum SCOPE_DOMAIN {
    USER = 'USER',
    CLIENT = 'CLIENT',
    DEVELOPER = 'DEVELOPER',
    CONSENT = 'CONSENT'
}

export class Scope {
    domain: SCOPE_DOMAIN;
    operation: string;

    constructor(domain: SCOPE_DOMAIN, operation: string) {
        this.domain = domain;
        this.operation = operation;
    }

    toString() {
        return this.domain + ':' + this.operation;
    }
}

export const SCOPE_USER_ALL = SCOPE_DOMAIN.USER + ':*';
export const SCOPE_DEVELOPER_ALL = SCOPE_DOMAIN.DEVELOPER + ':*';

/**
 * Parses a scope of the form {domain}:{operation}
 * @param id
 */
export function parseScope(id: string): Scope {
    const elements = id.split(':');
    if (elements.length != 2) throw new Error('Not a valid {domain}:{operation} scope : ' + id);

    if (!Object.values(SCOPE_DOMAIN).includes(elements[0] as any)) throw new Error('Unknown scope domain: ' + elements[0]);

    return new Scope(elements[0] as SCOPE_DOMAIN, elements[1]);
}

export const IDENTITY_PROVIDER_GOOGLE = 'https://www.googleapis.com';

/**
 * Possible types of POD providers.
 */
export const POD_IDENTITY_PROVIDERS: Record<string, string> = {
    //Google: GOOGLE_IDENTITY_PROVIDER_ID,
    //Solid: SOLID_IDENTITY_PROVIDER_ID
};

export function registerIdentityProvider(providerId: string) {
    POD_IDENTITY_PROVIDERS[providerId] = providerId;
}

export type POD_IDENTITY_PROVIDER = keyof typeof POD_IDENTITY_PROVIDERS;

// PROVIDER_TYPE is used to enumerate the possible authentication sources in passport.
// It is made obsolete by the data providers being configurable, hence the DATASOURCE_IDENTITY_PROVIDER can be any string.
export type DATASOURCE_IDENTITY_PROVIDER = string;
export type USER_IDENTITY_PROVIDER = POD_IDENTITY_PROVIDER | DATASOURCE_IDENTITY_PROVIDER;

export enum DEVELOPER_IDENTITY_PROVIDER {
    Github = 'github',
    OAuth = 'oauth'
}

export type IDENTITY_PROVIDER = USER_IDENTITY_PROVIDER | DEVELOPER_IDENTITY_PROVIDER;
export type PodDescriptor = {
    /** Description of this pod. Optional */
    description?: string;

    /** When this pod was registered in this user account.
     *  ISO date, Optional. */
    registrationDate?: string;

    /** Provider type for this pod */
    type: POD_IDENTITY_PROVIDER;

    /** Unique identifier for this pod */
    uri: string;

    // TODO should there be a separate folder subpath (e.g. for a Solid pod, )

    /** Identity to use to authenticate to this pod */
    identity: {
        // TODO support WAC-based authentication with DV identity

        /** Authentication scheme */
        type: 'token' | 'wac' | 'uma_grant';

        /** Identity identifier as recognized by the authentication scheme */
        id: string; // webID in case of Solid

        /** Identity issuer URI. */
        issuerUri?: string;

        /* Access grant URL, when the pod is authorized by the means of an UMA grant*/
        accessGrantUrl?: string;
    };
};
// TODO deprecated - remove
export type UserInfo = {
    'dt:createdAt': {
        'time:inXSDDateTimeStamp': string;
        '@type': 'time:Instant';
    };
    '@id': string;
    '@type': 'dt:DataOwner'; // TODO what are possible values ?
};
export type Identity<T extends IDENTITY_PROVIDER> = {
    id: string;
    provider: T;
    issuerUri?: string;
    type?: 'token' | 'wac' | 'uma_grant';
    firstName?: string;
    lastName?: string;
    userName?: string;
    name?: string;
    emails?: Array<{
        value: string;
        type?: string;
    }>;
    displayName?: string;
    profileUrl?: string;
    tokens?: {
        accessToken: string;
        refreshToken: string;
        scope: string[];
    }[];
};

export type AuthenticationInfo<T extends IDENTITY_PROVIDER> = TokenAuthenticationInfo<T> | GrantAuthenticationInfo<T>;

export type TokenAuthenticationInfo<T extends IDENTITY_PROVIDER> = {
    accessToken?: string;
    refreshToken: string;
    grantedScope: string[];
    podUrl?: string;
    identity: Identity<T>;
    roles?: string[];
};

export type GrantAuthenticationInfo<T extends IDENTITY_PROVIDER> = {
    accessGrantUrl: string;
    grantedScope: string[];
    podUrl?: string;
    identity: Identity<T>;
};
