import { IConsentsServices, UnprefixedForPersonalData } from '../../core';
import { HttpServiceClient } from '../http-client';
import { Consent } from '../../types';

export class HttpConsentsServices extends HttpServiceClient implements IConsentsServices {
    getConsent(consentId: string): Promise<Consent | undefined> {
        return this.httpClient.getJson(`/consents/${consentId}`);
    }

    getConsents(
        userId?: string,
        clientId?: string,
        applicationId?: string,
        consentReceiptId?: string,
        isActive?: boolean
    ): Promise<Consent[]> {
        return this.httpClient.getJson('/consents', {
            query: {
                userId,
                clientId,
                applicationId,
                consentReceiptId,
                isActive
            }
        });
    }

    createConsent(
        userId: string,
        consentReceiptUrl: string,
        duration: number,
        forPersonalData: UnprefixedForPersonalData[],
        startProcessingJob?: boolean
    ): Promise<Consent> {
        return this.httpClient.postJson(`/consents`, {
            userId,
            consentReceiptUrl,
            forPersonalData,
            duration,
            startProcessingJob
        });
    }

    withdrawConsent(consentId: string): Promise<Consent> {
        // TODO would it be better to use HTTP DELETE for this ?
        return this.httpClient.postJson(`/consents/withdraw`, {
            consentId
        });
    }
}
