export enum STREAMS {
    events = 'events',
    system = 'system'
}

export interface ICageServices {
    getEvents(stream_name?: STREAMS): Promise<any[]>;

    postEvent(event: any, stream_name?: STREAMS): Promise<any>;

    getAttestation(): Promise<any>;

    uploadData(filename: string, data: Blob): Promise<any>;

    listData(): Promise<{
        filenames: string[];
    }>;

    listOutputs(): Promise<{
        files: [{ file: string; size: number; atime: number; mtime: number; ctime: number }];
    }>;

    getOutput(filename: string): Promise<Blob>;
}
